<template>
  <v-app class="app">
    <sidebar class="sidebar" @show="changePage($event, 'sidebar')" />
    <contents
      ref="content"
      class="content"
      :page="page"
      @show="changePage($event, 'content')"
    />
  </v-app>
</template>

<script>
import Contents from './components/Contents.vue'

import Sidebar from './components/Sidebar.vue'

export default {
  name: 'App',

  components: {
    Contents,
    Sidebar,
  },
  data() {
    return {
      // page: null,
      page: 'donor',
    }
  },
  methods: {
    changePage(e, from) {
      if (e === 'club' && from === 'sidebar') {
        this.$refs.content.donorId = -1
      }
      this.page = e
    },
  },
}
</script>
