.app {
  position: relative;
}
.sidebar {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  width: 428px;
  /* background: #e4dbd5; */
  background: #dabdab;
}
.content {
  margin-left: 428px;
  padding: 80px 100px;
}
</style>
