<template>
  <div>
    <Club
      ref="club"
      @register="donorId = $event"
      :id="donorId"
      v-if="page === 'club'"
    />
    <Crimson v-if="page === 'crimson'" />
    <Article v-if="page === 'article'" />
    <SportStar v-if="page === 'sports'" />
    <Donor @checkDonor="checkDonor" v-if="page === 'donor'" />
  </div>
</template>
<script>
import Club from '../view/Club.vue'
import Crimson from '../view/Crimson.vue'
import SportStar from '../view/SportStar.vue'
import Article from '../view/Article.vue'
import Donor from '../view/Donor.vue'
export default {
  props: ['page'],
  mounted() {
    console.log(this.page)
  },
  data() {
    return {
      donorId: -1,
    }
  },
  components: { Crimson, SportStar, Article, Club, Donor },
  methods: {
    checkDonor(id) {
      this.donorId = id
      this.$nextTick(() => {
        this.$emit('show', 'club')
      })
    },
  },
}
</script>

